import React from "react";
import Prism from "prismjs";
import classNames from "classnames";
import { Container } from "reactstrap";
import ReactMarkDown from "react-markdown";

import NavBar from "../navbar/NavBar";
import "prismjs/themes/prism-okaidia.css";

class Content extends React.Component {
  constructor (props) {
    super(props);
  }

  flatten = (text, child) => {
    return typeof child === "string"
      ? text + child
      : React.Children.toArray(child.props.children).reduce(this.flatten, text);
  }

  headingRenderer = (props) => {
    let children = React.Children.toArray(props.children);
    let text = children.reduce(this.flatten, "");
    let slug = text.toLowerCase().replace(/\W/g, "-");

    return React.createElement("h" + props.level, {id: slug}, props.children);
  }

  render () { 
    const { markdown, toggle } = this.props;

    return (
      <Container fluid className="content">
        <NavBar toggle={toggle} />
        <ReactMarkDown 
          source={ markdown } 
          escapeHtml={false}
          renderers={{ heading: this.headingRenderer }}
          />
      </Container>
    );
  }
}

export default Content;
