import React from "react";
import classNames from "classnames";
import { NavItem, NavLink, Nav } from "reactstrap";
import SubMenu from "./SubMenu";

class SideBar extends React.Component {
  constructor(props) {
    super(props);
  }

  items () {
    return [
      {
        link: "/",
        text: "Introduction"
      },
      {
        link: "/#--features",
        text: "Features"
      },
      {
        link: "/#--prerequisite",
        text: "Prerequisite"
      },
      {
        link: "/#--installation",
        text: "Installation"
      },
      {
        link: undefined,
        text: "Architecture",
        subs: [
          {
            link: "/#--web-architecture",
            text: "Web"
          },
          {
            link: "/#--api-architecture",
            text: "API"
          },
          {
            link: "/#--mobile-architecture",
            text: "Mobile"
          }
        ]
      },
      {
        link: undefined,
        text: "Commands",
        subs: [
          {
            link: "/#--create-command",
            text: "Create"
          },
          {
            link: "/#--run-command",
            text: "Run"
          },
          {
            link: "/#--check-command",
            text: "Check"
          },
          {
            link: "/#--upload-command",
            text: "Upload"
          }
        ]
      },      
      {
        link: undefined,
        text: "Links",
        subs: [
          {
            link: "https://npmjs.com/package/ga-wdio",
            text: "NPM",
            target: "_blank"
          },
          {
            link: "https://github.com/geekyants/ga-wdio",
            text: "GitHub",
            target: "_blank"
          },
          {
            link: "https://github.com/geekyants/ga-wdio/issues",
            text: "Issues",
            target: "_blank"
          }
        ]
      },
      {
        link: "/#--support",
        text: "Support"
      }
    ];
  };

  render () {
    const { isOpen, toggle } = this.props;

    return (
      <div className={
        classNames("sidebar position-fixed vh-100", {"is-open": isOpen})
      }>
        {/* Heading */}
        <div className="sidebar-header">
          <span color="info" 
            onClick={toggle} 
            style={{color: "#fff"}}>&times;</span>
          <h3>GA-WDIO</h3>
        </div>

        <div className="side-menu">
          <Nav vertical className="list-unstyled pb-3">
            {
              this.items().map(item => {
                if (item.subs) {
                  return <SubMenu key={item.text} title={item.text} items={item.subs}/>;
                }
                return (
                  <NavItem key={item.link}>
                    <NavLink href={item.link} 
                      to={item.link} 
                      target={item.target || ""}>{item.text}</NavLink>
                  </NavItem>
                )
              })
            }
          </Nav>        
        </div>
      </div>
    )
  }
};

export default SideBar;
